<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';

import "swiper/css/navigation";

//import "./style.css";

import { Navigation } from "swiper";


export default {

  data(){
    return{
      loanAmount: 10000,
      loanDuratin: 6,
      BiWeeklyPayment: null,
      MonthlyPayment: null,
      WeeklyPayment: null,
      backgroundSize: '0% 100%',
      backgroundSize2: '0% 100%',
    }
  },


  methods: {
     updateSlider(e) {
            let clickedElement = e.target,
                min = clickedElement.min,
                max = clickedElement.max,
                val = clickedElement.value;

            this.backgroundSize = (val - min) * 100 / (max - min) + '% 100%';
     },
     updateSlider2(e) {
            let clickedElement = e.target,
                min = clickedElement.min,
                max = clickedElement.max,
                val = clickedElement.value;

            this.backgroundSize2 = (val - min) * 100 / (max - min) + '% 100%';
     },
     uptadeActiveButton(){
      var languageOptions = document.querySelectorAll('.button');

      languageOptions.forEach(function(element) {
        element.addEventListener('click', switchLanguage);
      });

      function switchLanguage (event) {
        var targetElement = event.target;

        languageOptions.forEach(function(element) {
          element.classList.remove('button-active');
        });

        targetElement.classList.add('button-active');
      }
     },
     
   },

   computed: {
      uptadeMonthlyPayment: function(){
        return this.MonthlyPayment = Math.round(this.loanAmount / this.loanDuratin);
      }, 
      uptadeBiWeeklyPayment: function(){
        return this.BiWeeklyPayment = Math.round((this.loanAmount / this.loanDuratin) / 2);
      },
      uptadeWeeklyPayment: function(){
        return this.WeeklyPayment = Math.round((this.loanAmount / this.loanDuratin) / 4);
      } 
    },

  
  components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      return {
        modules: [Navigation],
      };
  },
};

</script>

